*, *::before, *::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.list-unstyled {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}
.section-title {
  margin-top: 0;
  margin-bottom: 21px;
  text-align: center;
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  letter-spacing: -0.5%;
}

@media (max-width: $tablet-width) {
  .section-title {
    font-size: 24px;
    line-height: 41px;
  }
}
